.am-tab-bar {
  height: 100%;
  overflow: hidden;
}
.am-tab-bar-bar {
  position: relative;
  box-sizing: border-box;
  height: 50px;
  border-top: 1PX solid #ddd;
  width: 100%;
  display: flex;
  transition-duration: 0.2s;
  transition-property: height bottom;
  z-index: 100;
  justify-content: space-around;
  align-items: center;
  bottom: 0;
}
@media (min-resolution: 2dppx) {
  html:not([data-scale]) .am-tab-bar-bar {
    border-top: none;
  }
  html:not([data-scale]) .am-tab-bar-bar::before {
    content: '';
    position: absolute;
    background-color: #ddd;
    display: block;
    z-index: 1;
    top: 0;
    right: auto;
    bottom: auto;
    left: 0;
    width: 100%;
    height: 1PX;
    transform-origin: 50% 50%;
    transform: scaleY(0.5);
  }
}
@media (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  html:not([data-scale]) .am-tab-bar-bar::before {
    transform: scaleY(0.33);
  }
}
.am-tab-bar-bar-hidden-top {
  bottom: 50px;
  height: 0;
}
.am-tab-bar-bar-hidden-bottom {
  bottom: -50px;
  height: 0;
}
.am-tab-bar-bar .am-tab-bar-tab {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
}
.am-tab-bar-bar .am-tab-bar-tab-image {
  width: 22px;
  height: 22px;
  vertical-align: middle;
}
.am-tab-bar-bar .am-tab-bar-tab-title {
  font-size: 10px;
  margin: 3px 0 0 0;
  line-height: 1;
  text-align: center;
}
.am-tab-bar-bar .am-tab-bar-tab-icon {
  display: flex;
  justify-content: center;
}
.am-tab-bar-bar .am-tab-bar-tab-icon .tab-badge :last-child {
  margin-top: 4px;
  left: 22px;
}
.am-tab-bar-bar .am-tab-bar-tab-icon .tab-dot :last-child {
  margin-top: 4px;
  left: 22px;
}
.am-tab-bar-item {
  height: 100%;
}
